.icon {
  max-height: 64px;
  max-width: 64px; }

th {
  font-weight: inherit;
  text-align: left; }

html,
body {
  height: 100%;
  overflow: hidden; }

.container {
  height: 100%;
  width: 100%; }

.application, .application_view {
  align-content: center;
  align-items: center;
  display: flex;
  flex: 1;
  justify-content: center;
  height: 100%;
  width: 100%; }

.application_content {
  align-items: center;
  display: flex;
  flex: 1 0 auto;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
  position: relative; }

.application_panel {
  display: flex;
  bottom: 0;
  left: 0;
  position: absolute;
  right: 0;
  top: 0; }
  .application_panel--torrent-list {
    transition: transform 0.5s;
    z-index: 2; }
    .application_panel--torrent-list.is-open {
      transform: translateX(85%); }
  .application_panel--torrent-details {
    right: 15%;
    width: 85%;
    z-index: 1; }

body {
  background: #1d2938; }

ul {
  list-style: none; }

.application_sidebar {
  background: #1d2938;
  box-shadow: 1px 0 rgba(2, 3, 4, 0.3);
  color: #526780;
  flex: 1;
  min-width: 200px;
  max-width: 240px;
  overflow: auto;
  position: relative;
  z-index: 2; }

.sidebar__icon-button {
  color: rgba(82, 103, 128, 0.7);
  display: block;
  font-size: 0.8em;
  line-height: 1;
  padding: 10px;
  position: relative;
  transition: color 0.25s; }
  .sidebar__icon-button:hover {
    color: #258de5; }
    .sidebar__icon-button:hover .icon {
      fill: #258de5; }
  .sidebar__icon-button--interactive {
    cursor: pointer; }
  .sidebar__icon-button .icon {
    fill: #526780;
    height: 13px;
    transition: fill 0.25s;
    position: relative;
    top: -1px;
    vertical-align: middle;
    width: 13px; }

.sidebar__actions {
  display: flex;
  padding: 5px;
  justify-content: flex-start; }

.dropdown--speed-limits .dropdown__content {
  min-width: 180px; }
  .dropdown--speed-limits .dropdown__content .sidebar__icon-button {
    padding: 10px; }
    .dropdown--speed-limits .dropdown__content .sidebar__icon-button .icon {
      margin-right: 5px; }

.dropdown--speed-limits .dropdown__items {
  font-size: 0.9em; }
